/*
#styleguide
Title - h1
.ttlLv01 -【block】デフォルト, 2行, サブコピー付き
.ttlLv01-subCopy -【element】サブコピー
.ttlLv01-mainCopy -【element】メインコピー

```
<h1 class="ttlLv01">保険商品一覧</h1>
<h1 class="ttlLv01">
    保険金・給付金の<br />
    ご請求手続きについて
</h1>
<h1 class="ttlLv01">
    <span class="ttlLv01-subCopy">加入状況を選ぶ</span>
    <span class="ttlLv01-mainCopy">初めて保険を検討する</span>
</h1>
```

*/
.ttlLv01 {
    font-size: 46px;
    font-weight: 600;
    line-height: 56px;
    text-align: center;
    &-subCopy {
        display: block;
        margin-bottom: 5px;
        font-size: 22px;
        line-height: 22px;
        color: $life-primary;
    }
    &-mainCopy {
        display: block;
    }
    &::after {
        display: block;
        content: "";
        width: 100px;
        height: 2px;
        margin: 13px auto 0;
        background-color: $life-primary;
    }
    &.ttlLv01_insurance {
        .ttlLv01-subCopy {
            color: $life-primary;
        }
        &::after {
            background-color: $life-primary;
        }
    }
}

/*
#styleguide
Title - h1 Insurance

```
<h1 class="ttlLv01 ttlLv01_insurance">保険商品一覧</h1>
<h1 class="ttlLv01 ttlLv01_insurance">
    保険金・給付金の<br />
    ご請求手続きについて
</h1>
<h1 class="ttlLv01 ttlLv01_insurance">
    <span class="ttlLv01-subCopy">加入状況を選ぶ</span>
    <span class="ttlLv01-mainCopy">初めて保険を検討する</span>
</h1>
```

*/

/*
#styleguide
Title - h2
.ttlLv02 -【block】デフォルト, 2行, サブコピー付き
.ttlLv02-subCopy -【element】サブコピー
.ttlLv02-mainCopy -【element】メインコピー

```
<h2 class="ttlLv02">保障のタイプを選ぶ</h2>
<h2 class="ttlLv02">
    お客さまの声の受付から<br />
    改善までの流れ
</h2>
<h2 class="ttlLv02">
    <span class="ttlLv02-subCopy">POINT 2</span>
    <span class="ttlLv02-mainCopy">いつまで保証は必要ですか？</span>
</h2>
```

*/
.ttlLv02 {
    font-size: 32px;
    font-weight: 400;
    line-height: 40px;
    text-align: center;
    &-subCopy {
        display: block;
        margin-bottom: 4px;
        font-size: 16px;
        line-height: 16px;
        color: $life-primary;
    }
    &-mainCopy {
      display: block;
    }
}

/*
#styleguide
Title - h3
.ttlLv03 -【block】デフォルト, 2行, サブコピー付き
.ttlLv03-subCopy -【element】サブコピー
.ttlLv03-mainCopy -【element】メインコピー

```
<h3 class="ttlLv03">お客さまからの苦情内容の例</h3>
<h3 class="ttlLv03">
    多様なご要望にお応えする<br />
    加入方法の提供
</h3>
<h3 class="ttlLv03">
    <span class="ttlLv03-subCopy">先進医療による治療を受けたときは、</span>
    <span class="ttlLv03-mainCopy">通算2,000万円まで補償！</span>
</h3>
```

*/
.ttlLv03 {
    font-size: 24px;
    font-weight: 600;
    line-height: 32px;
    text-align: center;
    &-subCopy {
        display: block;
        margin-bottom: 4px;
        font-size: 16px;
        line-height: 16px;
    }
    &-mainCopy {
        display: block;
        color: $life-primary;
    }
}

/*
#styleguide
Title - h4

```
<h4 class="ttlLv04">お子さまのいるご家族(賃貸)のケース</h4>
<h4 class="ttlLv04">
    入院支援金額10万円、<br />
    先進医療特約付加の場合
</h4>
```

*/
.ttlLv04 {
    display: flex;
    align-items: stretch;
    font-size: 16px;
    font-weight: 300;
    line-height: 24px;
    &::before {
        display: block;
        content: "";
        width: 3px;
        margin-right: $base-space;
        background-color: $life-primary;
    }
}

/*
#styleguide
Title - h5

```
<h5 class="ttlLv05">3口ご加入で、さらに充実の保障</h5>
<h5 class="ttlLv05">
    持ち家の場合、多くの場合で住居費が<br />
    減額できるため、必要保障額も減額できます。
</h5>
```

*/
.ttlLv05 {
    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
}

@media screen and (max-width: 756px){
    .ttlLv01 {
        font-size: 28px;
        font-weight: 600;
        line-height: 35px;
        text-align: center;
        &-subCopy {
            display: block;
            margin-bottom: 6.5px;
            font-size: 16px;
            line-height: 16px;
            color: $life-primary;
        }
        &-mainCopy {
            display: block;
        }
        &::after {
            display: block;
            content: "";
            width: 100px;
            height: 2px;
            margin: 6.5px auto 0;
            background-color: $life-primary;
        }
        &.ttlLv01_insurance {
            .ttlLv01-subCopy {
                color: $life-primary;
            }
            &::after {
                background-color: $life-primary;
            }
        }
    }

    .ttlLv02 {
        font-size: 24px;
        font-weight: 400;
        line-height: 32px;
        text-align: center;
        &-subCopy {
            display: block;
            margin-bottom: 2px;
            font-size: 16px;
            line-height: 16px;
            color: $life-primary;
        }
        &-mainCopy {
          display: block;
        }
    }

    .ttlLv03 {
        font-size: 16px;
        font-weight: 600;
        line-height: 24px;
        text-align: center;
        &-subCopy {
            display: block;
            margin-bottom: 4px;
            font-size: 12px;
            line-height: 12px;
        }
        &-mainCopy {
            display: block;
            color: $life-primary;
        }
    }

    .ttlLv04 {
        display: flex;
        align-items: stretch;
        font-size: 16px;
        font-weight: 300;
        line-height: 24px;
        &::before {
            display: block;
            content: "";
            width: 3px;
            margin-right: $base-space;
            background-color: $life-primary;
        }
    }

    .ttlLv05 {
        font-size: 16px;
        font-weight: 600;
        line-height: 24px;
    }
}
