/*
#overview
Article
主にnews・media配下を中心に使用されているモジュール群。
*/

/*
#styleguide
Article - Title

```
<h2 class="articleTtl">おすすめの記事</h2>
<h3 class="articleH3">記事大見出しダミー記事大見出しダミー記事大見出しダミー記事大見出しダミー記事大見出しダミー</h3>
<h4 class="articleH4">記事中見出しダミー記事中見出しダミー記事中見出しダミー記事中見出しダミー記事中見出しダミー</h4>
```
*/
/* article - title */
.articleTtl{
    font-size: 32px;
    line-height: 1.25;
    margin-top: 30px;
    margin-bottom: 30px;
    text-align: center;
    font-weight: normal;
    &:first-child{
        margin-top: 0;
    }
    &.articleTtl_bd{
        padding-bottom: 30px;
        margin-bottom: 0;
        border-bottom: 1px solid $gray-light;
    }
    &.articleTtl_03{
        font-size: 24px;
        margin-top: 30px;
        padding: 15px;
        margin-top: 40px;
        background: #ebebeb;
        // border-top: 1px solid $gray;
    }
}
.articleTtl_03[aria-selected="true"] {
    .lstAcc-descTermSwitch::before {
        content: "閉じる";
    }
    .lstAcc-descTermSwitchIcon {
        transform: rotate(180deg);
    }
}
.articleTtl_03[aria-selected="false"] {
    .lstAcc-descTermSwitch::before {
        content: "詳しく見る";
    }
}

.articleH3{
    display: flex;
    align-items: stretch;
    font-size: 18px;
    font-weight: bold;
    line-height: 24px;
    margin-top: 30px;
    margin-bottom: 20px;
    clear: both;
    &::before {
        display: block;
        content: "";
        width: 3px;
        margin-right: $base-space;
        background-color: $brand-primary;
    }
}
.articleH4{
    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
    margin-top: 20px;
    margin-bottom: 10px;
}

// for products/index.html
body.page_products h2.articleTtl{
    border-top: 1px solid $gray;
    border-bottom: 1px solid $gray;
    padding: 10px;
}
body.page_products .articleH3{
    text-align: center;
    margin-bottom: 16px;
    color: $brand-primary;
    padding: 10px 0;
    display: block;
    &::before {
        display: inline-block;
        content: "";
        width: 72px;
        height: 1px;
        margin-right: 16px;
        background-color: $brand-primary;
        vertical-align: middle;
    }
    &::after {
        display: inline-block;
        content: "";
        width: 72px;
        height: 1px;
        margin-left: 16px;
        background-color: $brand-primary;
        vertical-align: middle;
    }
}

// for link/internal.html
.articleTtl-link{
    font-size: 32px;
    line-height: 1.25;
    margin-bottom: 30px;
    color: $brand-primary;
}
.articleJump-link{
    font-weight: bold;
    margin-bottom: 10px;
}
.articleTxt-link{}
.articleBtn-link{
    text-align: center;
    margin-top: 30px;
    a{
        border: 1px solid $brand-primary;
        display: inline-block;
        padding: 5px 15px;
        border-radius:4px ;
        color: $brand-primary;
        text-decoration: none;
        width: 320px;
        max-width: 100%;
        margin: 10px 8px;
        -moz-box-sizing: border-box;
        -webkit-box-sizing: border-box;
        -o-box-sizing: border-box;
        box-sizing: border-box;
        &:hover{
          background: $brand-primary;
          color: $white;
        }
        &:after{
          color: inherit;
          font-family: rex-icon;
          content: "\F127";
          font-size: 14px;
          text-decoration: none;
          margin-left: 5px;
        }
    }
}

// for error404.html
.articleTtl-error{
    font-size: 32px;
    line-height: 1.25;
    margin-bottom: 30px;
    text-align: center;
}
.articleTxt-error{
    text-align: center;
}
.articleBtn-error{
  text-align: center;
  margin-top: 30px;
  a{
        border: 1px solid $brand-primary;
        display: inline-block;
        padding: 5px 15px;
        border-radius:4px ;
        color: $brand-primary;
        text-decoration: none;
        width: 320px;
        max-width: 100%;
        margin: 0 8px;

        -moz-box-sizing: border-box;
        -webkit-box-sizing: border-box;
        -o-box-sizing: border-box;
        box-sizing: border-box;
        &:hover{
          background: $brand-primary;
          color: $white;
        }
        &:after{
          color: inherit;
          font-family: rex-icon;
          content: "\F127";
          font-size: 14px;
          text-decoration: none;
          margin-left: 5px;
        }
  }
}

/*
#styleguide
Article - Lead / Table Of Contents

```
<p class="articleLead">リードリードリードリードリードリードリードリードリードリードリードリードリードリードリードリードリードリード</p>
<h2 class="articleToc">目次ダミーテキスト目次ダミーテキスト目次ダミーテキスト目次ダミーテキスト</h2>
```
*/
/* article - lead */
.articleLead{
  font-weight: bold;
  margin-top: 20px;
  margin-bottom: 20px;
}

/* article - table of contents(目次) */
.articleToc{
  font-weight: bold;
  font-size: 24px;
  margin-top: 10px;
  margin-bottom: 30px;
}

/* article - detail */
.articleDetail{
  padding: 30px 0;
  border-top: 1px solid $gray;
  border-bottom: 1px solid $gray;
  .articleDetail-date{
    margin-bottom: 20px;
  }
  .articleDetail-ttl{
    font-size: 32px;
    margin-bottom: 20px;
    font-weight: bold;
  }
  .articleDetail-cat{
    margin-bottom: 20px;
    color: $brand-primary;
    a{
      color: inherit;
    }
  }
  .articleDetail-lead{
    font-size: 20px;
    font-weight: bold;
  }
}

