/* article - lead */
/* article -list */
.bg_gray .articleList{
    display: block;
}
.articleList{
    display: block;
    padding: 30px 50px 30px 0;
    color: $gray-darkest;
    position: relative;
    border-bottom: 1px solid $gray-light;
    &:hover{
        text-decoration:none;
        background-color: $gray-lightest;
    }
    .rex-icon{
        position: absolute;
        top: 50%;
        right: 0;
        line-height: 1;
        margin-top: -0.5em;
    }
    .articleList-link{}
    .articleList-date{
        margin-bottom: 10px;
    }
    .articleList-ttl{
        margin-bottom: 10px;
        font-weight: bold;
        a{
            color: inherit;
            display: block;
        }
    }
    .articleList-cat{
        margin-top: 10px;
        color: $brand-primary;
        a{
            color: inherit;
        }
    }
}

/* article - img */
.articleImg{
    text-align: center;
    margin-top: 30px;
    img{
        max-width:100%;
        height: auto;
    }
}
.articleImgTxt{
  text-align: center;
}

/*
#styleguide
Article - Box

```
<div class="articleBox">
    <p class="articleBox-img"><img src="https://placehold.jp/134x134.png" alt=""></p>
    <p class="articleBox-ttl">商品名商品名商品名商品名</p>
    <p class="articleBox-cat">生命保険</p>
    <p class="articleBox-txt">商品サマリ商品サマリ商品サマリ商品サマリ商品サマリ商品サマリ商品サマリ商品サマリ商品サマリ商品サマリ商品サマリ商品サマリ商品サマリ</p>
    <p class="articleBox-link"><a href="#">この商品を見る<i aria-hidden="true" class="rex-icon chevron-right"></i></a></p>
<!-- /.articleBox --></div>
```
*/

/* article - box */
.articleBox{
    border: 1px solid $gray-medium;
    padding: 20px;
    margin-top: 30px;
    .articleBox-img{
        float: left;
        margin-right: 20px;
    }
    .articleBox-ttl{
        font-weight: bold;
        overflow: hidden;
    }
    .articleBox-cat{
        color: $brand-primary;
        overflow: hidden;
    }
    .articleBox-txt{
        margin-top: 20px;
        overflow: hidden;
    }
    .articleBox-link{
        overflow: hidden;
        text-align: right;
        .rex-icon{
            font-size: 16px;
            margin-left: 5px;
        }
    }
}

/*
#styleguide
Article - Card

```
<div class="articleCard">
    <div class="articleCard-img">
        <img src="/media/assets/common/img/img_thum_004.png" alt="">
    </div>
    <div class="articleCard-inner">
        <div class="articleCard-top">
            <div class="articleCard-cat"><a href="#">カテゴリ</a></div>
                <div class="articleCard-tag"><a href="#" class="tag-parts">タグ</a><a href="#" class="tag-parts">タグ</a></div>
            </div>
            <div class="articleCard-ttl"><a href="">記事タイトル記事タイトル記事タイトル記事タイトル記事タイトル</a></div>
    <!-- /.articleCard-inner --></div>
<!-- /.articleCard --></div>
```
*/
/* article -card */
.articleCard{
    box-shadow: 0 5px 10px 0px rgba(0,0,0,.2);
    border-radius: 3px;
    overflow: hidden;
    height: 100%;
}
.articleCard-img{
    img{
        width: 100%;
        height: auto;
        cursor:pointer;
        -webkit-transition: all 0.3s ease;
        -moz-transition: all 0.3s ease;
        -o-transition: all 0.3s ease;
        transition: all 0.3s ease;
        &:hover{
            opacity: 0.7;
        }
    }
}
.articleCard-inner{
    padding: 20px;
}
.articleCard-top{
    overflow: hidden;
    margin-bottom: -5px;
}
.articleCard-bottom{
    margin-top: 10px;
    overflow: hidden;
}
.articleCard-cat{
    color: $brand-primary;
    float: left;
    margin-right: 10px;
    margin-bottom: 5px;
    a{
        color: $brand-primary;
    }
}
.articleCard-tag{
    float: left;
    margin-right: -10px;
    margin-top: -5px;
    margin-bottom: 10px;
}
.articleCard-tag{
    .tag-parts{
        margin-right: 10px;
        margin-top: 5px;
    }
}

.articleCard.articleCard_new .articleCard-ttl:before{
    content:"NEW";
    border: 1px solid $brand-primary;
    color: $brand-primary;
    border-radius: 4px;
    font-size: 10px;
    line-height: 1;
    padding: 5px 8px;
    margin-right: 8px;
    // position: absolute;
    // left: 0;
    // top: -25px;
    display: inline-block;
    vertical-align: middle;
    pointer-events: none;
}
.articleCard-ttl{
    margin-top: 15px;
    font-weight: bold;
    position: relative;
    &:first-child{
        margin-top: 0;
    }
    a{
        color: $gray-darkest;
        display:inline;
        vertical-align: middle;
    }
}
.articleCard-lead{
    margin-top: 10px;
    a{
        color: $gray-darkest;
    }
}

@media screen and (max-width: 756px){
    .articleLead{
        margin-bottom: 10px;
    }
    .articleH3{
        margin-top: 25px;
        margin-bottom: 10px;
    }
    .articleH4{
        margin-top: 15px;
        margin-bottom: 5px;
    }
    .articleBox{
        padding: 15px;
    }
    .articleBox .articleBox-img{
        width: 33%;
        margin-right: 15px;
    }
    .articleTtl{
        font-size: 23px;
        margin-top: 30px;
        margin-bottom: 15px;
    }
    .articleImg{
        margin-top: 20px;
        .ytMovie{
            width:100%;
            padding-bottom: 56.25%;
            height:0px;
            position: relative;
            display: block;
            iframe{
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
            }
        }
    }
    .articleCard-inner{
        padding: 15px;
    }
    .articleCard-lead{
        display: none;
    }
    .articleCard-ttl{
        margin-top: 10px;
    }
    .articleCard-img img{
        object-fit: cover;
        width: 100%;
        height: 100%;
    }
    .articleTtl-error{
      font-size: 24px;
      margin-bottom: 20px;
    }
    .articleTtl.articleTtl_bd{
      padding-bottom: 15px;
    }
    .articleDetail .articleDetail-date{
      margin-bottom: 10px;
    }
    .articleList .articleList-link{
      padding: 20px 30px 20px 0;
    }
    .articleBox .articleBox-txt{
      margin-top: 10px;
    }
    .articleDetail .articleDetail-cat{
      margin-bottom: 10px;
    }
    .articleDetail{
      padding: 20px 0;
    }
    .articleDetail .articleDetail-ttl{
      font-size: 16px;
      margin-bottom: 10px;
    }
    .articleTtl.articleTtl_03{
        margin-top: 30px;
        margin-bottom: 0;
        padding: 15px;
        background: $gray-lighter;
        border: none;
        font-size: 16px;
        text-align: left;
        &:first-child{
            margin-top: 0;
        }
    }
}

@media screen and (max-width: 320px){
    .articleTtl{
        font-size: 21px;
    }
    .articleBtn-link{
        a {
            margin: 10px 0;
        }
    }
    .articleBtn-error a {
        margin: 0;
    }
}
