/*
#overview
Title Display


*/

.ttlDisplay {
    text-align: center;
    &_type01 {
        font-size: 48px;
        font-weight: 600;
        line-height: 62px;
    }
    &_type02 {
        font-size: 46px;
        font-weight: 300;
        line-height: 60px;
    }
    &_type03 {
        font-size: 44px;
        font-weight: 300;
        line-height: 58px;
    }
    &_type04 {
        font-size: 42px;
        font-weight: 600;
        line-height: 56px;
    }
    &_type05 {
        font-size: 42px;
        font-weight: 600;
        line-height: 54px;
    }
    &-copy_important {
        font-weight: 600;
    }
}

/*
#styleguide
Title display - type01

```
<h1 class="ttlDisplay ttlDisplay_type01">
    「楽天」だからできる<br />
    新たな世界に挑む
</h1>
```

*/

