/*
#styleguide
Address

```
<address class="address">
    <strong class="address-company">楽天生命保険株式会社</strong><br />
    〒158-0094<br />
    東京都世田谷区玉川一丁目14番1号<br />
    楽天クリムゾンハウス<br />
    TEL: 0120-921-210（代表）<br />
    <a href="mailto:first.last@example.com" class="address-lnk">first.last@example.com</a>
</address>
```

*/
.address {
    font-size: 16px;
    font-weight: 400;
    line-height: 32px;
    font-style: normal;
}
.address-company {
    font-weight: bold;
}
.address-lnk {
    font-weight: 600;
    color: $lnk-txt;
    text-decoration: none;
    transition: color .2s $standard-curve;
    &:focus , &:hover {
        color: $lnk-txt-hover;
        text-decoration: none;
    }
    &:active {
        color: $lnk-txt-active;
    }
}
