.lstToggle {
    width: 864px;
    margin: 0 auto;
    font-size: 16px;
    font-weight: 400;
    line-height: 32px;;
}
.lstToggle-item {
    border-bottom: 1px solid #888888;
}
.lstToggle-desc {

}
.lstToggle-descTerm {
    display: flex;
    padding: 38px 0 28px;
    cursor: pointer;
    &[aria-selected="true"] {
        .lstToggle-descTermSwitch::before {
            content: "閉じる";
        }
        .lstToggle-descTermSwitchIcon {
            transform: rotate(180deg);
        }
    }
    &[aria-selected="false"] {
        .lstToggle-descTermSwitch::before {
            content: "続きを読む";
        }
    }
}
.lstToggle-descTermTtl {
    display: block;
    width: 208px;
    margin-right: 32px;
    color: $life-primary;
    font-size: 24px;
    font-weight: 600;
    line-height: 32px;
    text-align: center;
}
.lstToggle-descTermLead {
    position: relative;
    display: block;
    width: 624px;
    color: #888888;
    font-size: 24px;
    font-weight: 400;
    line-height: 32px;
}
.lstToggle-descTermTxt {
    display: block;
    margin-bottom: 48px;
}
.lstToggle-descTermSwitch {
    position: absolute;
    bottom: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    &::before {
        display: block;
        content: "続きを読む";
        margin-right: 8px;
        color: #888888;
        font-size: 16px;
        font-weight: 600;
        line-height: 16px;
    }
}
.lstToggle-descTermSwitchIcon {
    display: block;
    width: 24px;
    height: 24px;
    padding: 4px;
    background-color: #888888;
    border-radius: 12px;
    box-sizing: border-box;
    transition: transform .2s $standard-curve;
    .rex-icon {
        font-size: 16px;
        color: $white;
        vertical-align: top;
    }
}
.lstToggle-descDfn {
    display: none;
    width: 624px;
    margin-left: auto;
    color: #888888;
    font-size: 16px;
    font-weight: 400;
    line-height: 32px;
}
.lstToggle-btn {
    display: inline-block;
    padding: .75em 1em;
    font-family: 'Hiragino Kaku Gothic ProN', Meiryo, MS-PGothic, sans-serif;
    font-size: 16px;
    line-height: 16px;
    text-decoration: none;
    text-align: center;
    color: $gray-dark;
    vertical-align: baseline;
    background-color: $white;
    border: 1px solid $gray-dark;
    border-radius: $base-radius;
    box-sizing: border-box;
    cursor: pointer;
    transition: background-color .2s $standard-curve, color .2s $standard-curve;
    &:hover, &:focus {
        color: $white;
        background-color: $gray-medium;
        border: 1px solid $gray-medium;
    }
    &:active {
        color: $white;
        background-color: $gray-darkest;
        border: 1px solid $gray-darkest;
    }
    &.btn[disabled] , &.btn_disabled {
        color: $gray-light;
        background-color: $gray-lighter;
        border: 1px solid transparent;
        cursor: not-allowed;
        &:hover, &:focus, &:active {
            background-color: $gray-lighter;
            border: 1px solid transparent;
        }
    }
    &.btn_disabled {
        pointer-events: none;
    }
}
.lstToggle-innerlst {
    &.lstToggle-innerlst_circle {
        .lstToggle-innerlstitem {
            position: relative;
            color: $black;
            font-size: 16px;
            font-weight: 400;
            line-height: 32px;
            padding-left: 1.25em;
        }
        .lstToggle-innerlstitem::before {
            position: absolute;
            top: 0;
            left: 0;
            display: block;
            content: "●";
            color: $life-primary;
        }
    }
}
.lstToggle-btnGroup {
    margin: 40px 0;
}
.lstToggle-btnGroup_col01 {
    margin-left: 100px;
    margin-right: auto;
    .lstToggle-btnGroupItem {
    }
    .lstToggle-btn {
        width: 176px;
    }
}
.lstToggle-btnGroup_col02 {
    display: flex;
    justify-content: space-between;
    .lstToggle-btnGroupItem {
        width: calc(50% - 8px);
    }
    .lstToggle-btn {
        width: 100%;
    }
}

