*/
.lstToggle {
    &.lstToggle_box {
        width: 896px;
        padding: 0 16px;
        border: 1px solid #888888;
        box-sizing: border-box;
        .lstToggle-item {
            border-bottom: 0;
        }
        .lstToggle-descTerm {
            padding: 25px 0 28px;
        }
        .lstToggle-descTermTtl {
            width: 240px;
            margin-right: 0;
            font-size: 18px;
            font-weight: 600;
            line-height: 32px;
        }
        .lstToggle-descTermLead {
            color: $black;
            font-size: 18px;
            font-weight: 400;
            line-height: 32px;
        }
    }
}

